body {
  margin: 0;
  font-family: "Space Grotesk", Roboto, sans-serif;
}

.header {
  box-sizing: border-box;
  height: 100vh;
  width: 320px;
  background-image: url(../images/Image.png);
  background-size: cover;
  background-position: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  &__bottom {
    padding: 0 20px 40px;
  }

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 36px;
    line-height: 36px;
  }
}

.top-bar {
  padding: 24px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;

  &__icons {
    display: flex;
    gap: 24px;
  }

  &__logo {
    display: flex;
  }
}


.icon {
  display: flex;
  height: 24px;
  width: 24px;
  background-image: url(../images/Icon-Burger-menu.png);
}

.icon--phone {
  background-image: url(../images/Phone-number.png);
}

.icon--menu {
  background-image: url(../images/Icon-Burger-menu.png);
}

.icon--close {
  background-image: url(../images/Icon-Close.png);
}

.menu {
  box-sizing: border-box;
  height: 100vh;
  width: 320px;
  background-color: #E5E6E8;
  font-weight: 700;
  color: #292929;
  text-decoration: none;

  &__top {
    margin-bottom: 32px;
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__content {
    padding-inline: 20px;
  }

  &__phone-number{
    display: block;
    margin-bottom: 16px;
    font-size: 16px;
    line-height: 22.4px;
    color: #292929;
    text-transform: uppercase;
  }

  &__call-to-order {
    display: block;
    width: fit-content;
    padding-bottom: 7px;
    border-bottom: 1px solid #292929;
    color: #292929;
    text-transform: uppercase;

    font-size: 16px;
    line-height: 22.4px;
    letter-spacing: 2px;
    text-decoration: none;
  }
}


.nav__link {
  color: inherit;
  text-decoration: none;
  font-size: 22px;
  line-height: 22px;
  letter-spacing: 2px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 32px;
}
